<template lang="">

    <q-item clickable v-ripple @click="selectConversation" :active="isActive" 
    class="q-ma-xs rounded-borders" active-class="bg-grey-9 text-white">
        <q-item-section avatar>
            <!-- <q-avatar>
                <img :src="avatar">
            </q-avatar> -->
            <avatar :src="avatar" :type="conversation.type" />
        </q-item-section>

        <q-item-section>
            <q-item-label lines="1" v-if="conversation.type === TIM.TYPES.CONV_C2C" style="font-weight: bold; font-size: 0.8rem;">
                {{ formatName(conversation.userProfile.nick || conversation.userProfile.userID) }}
            </q-item-label>
            <q-item-label lines="1" v-else-if="conversation.type === TIM.TYPES.CONV_GROUP" style="font-weight: bold; font-size: 0.8rem;">
                {{ formatName(conversation.groupProfile.name || conversation.groupProfile.groupID) }}
            </q-item-label>
            <q-item-label lines="2" v-else-if="conversation.type === TIM.TYPES.CONV_SYSTEM" style="font-weight: bold; font-size: 0.8rem;">
                系统通知
            </q-item-label>

            <!-- <div v-if="conversation.lastMessage"> -->
            <q-item-label class="conversation__summary" caption v-if="conversation.lastMessage && hasMessageAtMe">
                {{ messageAtMeText }}
            </q-item-label>
            <q-item-label class="conversation__summary" caption v-if="conversation.lastMessage">
                <q-badge color="red" v-if="showUnreadCount">
                    {{conversation.unreadCount > 99 ? '99+' : conversation.unreadCount}}
                </q-badge>
                {{ messageForShow }}
            </q-item-label>
            <!-- </div> -->
        </q-item-section>

        <q-item-section side>
            <q-item-label caption>
                {{date}}
            </q-item-label>
            <q-btn icon="keyboard_arrow_down" round flat size="xs">
                <q-menu transition-show="jump-down" transition-hide="jump-up" :offset="[10, 0]" class="bg-grey-10">

                    <q-list dense style="min-width: 30px;">
                        <q-item clickable v-close-popup>
                            <q-item-section @click="deleteConversation">
                                <q-icon name="close" />
                            </q-item-section>
                        </q-item>

                    </q-list>
                </q-menu>
            </q-btn>
            <!-- <q-badge rounded color="red" v-if="showUnreadCount">
                {{conversation.unreadCount > 99 ? '99+' : conversation.unreadCount}}
            </q-badge> -->
        </q-item-section>
    </q-item>

</template>
<script>
    import { computed, getCurrentInstance, ref } from 'vue'
    import { useStore } from 'vuex'
    import { isToday, getDate, getTime } from '../../utils/date'
    import Avatar from '../Avatar.vue'
    export default {
        components: {
            Avatar
        },
        props: ['conversation'],
        setup(props) {
            const { appContext: { config: { globalProperties: { tim, TIM } } } } = getCurrentInstance()
            const store = useStore()
            const currentConversation = computed(() => store.state.conversation.currentConversation)
            // const toAccount = computed(() => store.getters.toAccount)

            const currentUserProfile = computed(() => store.state.user.currentUserProfile)
            let popoverVisible = ref(false)
            // let showMessageAtMe_text = ref('')



            const hasMessageAtMe = computed(() => {
                return (currentConversation.value.conversationID !== props.conversation.conversationID
                    && props.conversation.groupAtInfoList && props.conversation.groupAtInfoList.length > 0)
            })

            const MessageAtMeText = computed(() => {
                let text = ''
                if (props.conversation.groupAtInfoList.length > 0) {
                    props.conversation.groupAtInfoList.forEach((item) => {
                        if (item.atTypeArray[0] === TIM.TYPES.CONV_AT_ME) {
                            text.indexOf('[@所有人]') !== -1 ? text = '[@所有人][有人@我]' : text = '[有人@我]'
                        }
                        if (item.atTypeArray[0] === TIM.TYPES.CONV_AT_ALL) {
                            text.indexOf('[有人@我]') !== -1 ? text = '[有人@我][@所有人]' : text = '[@所有人]'
                        }
                        if (item.atTypeArray[0] === TIM.TYPES.CONV_AT_ALL_AT_ME) {
                            text = '[@所有人][有人@我]'
                        }
                    })
                }
                return text
            })

            const showUnreadCount = computed(() => {

                // if (store.getters.hidden) {
                //     return props.conversation.unreadCount > 0
                // }


                // 是否显示未读计数。当前会话和未读计数为0的会话，不显示。
                return (
                    currentConversation.value.conversationID !==
                    props.conversation.conversationID && props.conversation.unreadCount > 0
                )
            })

            // store的东西都是响应式的

            const date = computed(() => {
                if (
                    !props.conversation.lastMessage ||
                    !props.conversation.lastMessage.lastTime
                ) {
                    return ''
                }
                const date = new Date(props.conversation.lastMessage.lastTime * 1000)
                if (isToday(date)) {
                    return getTime(date)
                }
                return getDate(date)
            })

            const avatar = computed(() => {
                switch (props.conversation.type) {
                    case 'GROUP':
                        return props.conversation.groupProfile.avatar
                    case 'C2C':
                        return props.conversation.userProfile.avatar
                    default:
                        return ''
                }
            })
            const conversationName = computed(() => {
                if (props.conversation.type === TIM.TYPES.CONV_C2C) {
                    return props.conversation.userProfile.nick || props.conversation.userProfile.userID
                }
                if (props.conversation.type === TIM.TYPES.CONV_GROUP) {
                    return props.conversation.groupProfile.name || props.conversation.groupProfile.groupID
                }
                if (props.conversation.type === TIM.TYPES.CONV_SYSTEM) {
                    return '系统通知'
                }
                return ''
            })

            /* const showGrayBadge = computed(() => {
                if (props.conversation.type !== TIM.TYPES.CONV_GROUP) {
                    return false
                }
                return (
                    props.conversation.groupProfile.selfInfo.messageRemindType ===
                    'AcceptNotNotify'
                )
            }) */

            const messageForShow = computed(() => {
                if (props.conversation.lastMessage.isRevoked) {
                    if (props.conversation.lastMessage.fromAccount === currentUserProfile.value.userID) {
                        return '你撤回了一条消息'
                    }
                    if (props.conversation.type === TIM.TYPES.CONV_C2C) {
                        return '对方撤回了一条消息'
                    }
                    return `${props.conversation.lastMessage.fromAccount}撤回了一条消息`
                }
                return props.conversation.lastMessage.messageForShow
            })

            function selectConversation() {
                if (props.conversation.conversationID !== currentConversation.value.conversationID) {
                    store.dispatch(
                        'checkoutConversation',
                        props.conversation.conversationID
                    )
                }
            }

            function deleteConversation(event) {
                // 停止冒泡，避免和点击会话的事件冲突
                event.stopPropagation()
                tim.deleteConversation(props.conversation.conversationID)
                    .then(() => {
                        store.commit('showMessage', {
                            message: `会话【${conversationName.value}】删除成功!`,
                            type: 'success'
                        })
                        popoverVisible.value = false
                        store.commit('resetCurrentConversation')
                    })
                    .catch(error => {
                        store.commit('showMessage', {
                            message: `会话【${conversationName.value}】删除失败!, error=${error.message}`,
                            type: 'error'
                        })
                        popoverVisible.value = false
                    })
            }

    

            // function showContextMenu() {
            //     popoverVisible.value = true
            // }

            const isActive = computed(() => {

                if (props.conversation.conversationID == currentConversation.value.conversationID) {

                    return true
                }
                return false
            })

            function formatName(name) {
                if (name.length > 20) {
                    return name.slice(0, 20) + '...'
                }
                return name
            }
            return {
                selectConversation,
                avatar, messageForShow, date, MessageAtMeText, hasMessageAtMe,
                showUnreadCount,
                deleteConversation, isActive, formatName
            }

        }
    }
</script>
<style lang="">

</style>