<template lang="">
    <text-element v-if="message.type === TIM.TYPES.MSG_TEXT" :isMine="isMine()" :payload="message.payload"
        :avatar="avatarSrc" :date="date" :name="showName">
    </text-element>
</template>
<script>
    // import systemAvatar from '../../assets/image/system.png'
    import { getDate, isToday, getTime } from '../../utils/date'
    // import { useStore } from 'vuex'
    import { getCurrentInstance, computed } from 'vue'
    import TextElement from './message-elements/TextElement.vue'
    // import { useStore } from 'vuex'

    export default {
        name: 'MessageItem',
        components: {
            TextElement,
        },
        props: {
            message: {
                type: Object,
                required: true
            },
            conversationType: {
                
            }
        },

        setup(props, context) {
            // const store = useStore()
            const { appContext: { config: { globalProperties: { TIM } } } } = getCurrentInstance()

            // const currentConversation = computed(() => store.state.conversation.currentConversation)
            // const currentUserProfile = computed(() => store.state.user.currentUserProfile)
            // const currentConversation = computed(() => store.state.conversation.currentConversation)
            const avatarSrc = computed(() => {
                    return props.message.avatar ? props.message.avatar : 'https://tspace-1253470014.cos.ap-hongkong.myqcloud.com/nft/confienergy/image/coolzai.gif'
            })
            context.emit('loaded')
            // const defaultSrc = computed(() => {
            //     switch (currentConversation.value.type) {
            //         // 个人头像
            //         case 'C2C':
            //             return 'https://imgcache.qq.com/open/qcloud/video/act/webim-avatar/avatar-2.png'
            //         // 群默认头像 
            //         case 'GROUP':
            //             return 'https://imgcache.qq.com/open/qcloud/video/act/webim-avatar/avatar-3.png'
            //         case TIM.TYPES.CONV_SYSTEM:
            //             return systemAvatar
            //         default:
            //             // 默认头像
            //             return 'https://imgcache.qq.com/open/qcloud/video/act/webim-avatar/avatar-1.png'
            //     }
            // })

            const showName = computed(() => {
                switch(props.message.conversationType) {
                    case 'C2C':
                        return  ''
                    case 'GROUP':
                        return props.message.nick || props.message.from.substring(0,8)
                    default: 
                        return ''
                }
            })

            const date = computed(() => {
                const date = new Date(props.message.time * 1000)
                if(isToday(date)) {
                    return getTime(date)
                }
                return getDate(date)
            })
            function isMine() {
                return props.message.flow === 'out'
            }

            return {
                isMine, TIM, avatarSrc, date, showName
            }
        }

    }
</script>
