<template lang="">
    <q-scroll-area style="height: 100vh;" :thumb-style="thumbStyle" :visible="false">
        <!-- <q-scroll-area style="height: calc(100% - 100px)"> -->
        <q-list>
            <nft-item v-for="nft in nftList" :key="nft" :nft="nft" @toggleGroupDialog="toggleGroupDialog"></nft-item>

        </q-list>


        <q-dialog v-model="groupDialog" persistent transition-show="flip-down" transition-hide="flip-up">
            <q-card class="bg-dark text-white" style="width: 400px">
                <q-card-section>
                    <div class="text-h6 text-center">创建群组</div>

                </q-card-section>

                <q-card-section class="q-pt-none text-center">
                    <div class="q-pa-md" style="max-width: 400px">

                        <q-form @submit="onSubmit" @reset="onReset" class="q-gutter-md">
                            <q-input filled v-model="name" placeholder="群组ID *" lazy-rules
                                :rules="[ val => val && val.length > 0 || '输入群组ID']" />

                            <q-input filled type="string" v-model="age" placeholder="群名称 *" lazy-rules :rules="[
                              val => val !== null && val !== '' || '请输入群名称',
                              val => val > 0 && val < 100 || '请输入合规的群名称'
                            ]" />

                            <q-file color="lime-11" filled v-model="model" label="群头像 * ">
                                <template v-slot:prepend>
                                    <q-icon name="attachment" />
                                </template>
                            </q-file>

                            <div>
                                <q-btn label="Submit" type="submit" color="grey-9" />
                            </div>
                        </q-form>

                    </div>
                </q-card-section>

                <q-card-actions align="right" class="text-white">
                    <q-btn flat label="Close" v-close-popup />
                </q-card-actions>

            </q-card>

        </q-dialog>
    </q-scroll-area>
</template>
<script>
    import nftItem from '../nft/NFTItem'
    import { computed, ref } from 'vue'
    import { useStore } from 'vuex'
    export default {
        components: {
            nftItem
        },
        setup() {
            const store = useStore()
            const nftList = computed(() => store.state.nftList)
            console.log(nftList)
            const groupDialog = ref(false);
            function toggleGroupDialog() {
                groupDialog.value = true
            }

            const thumbStyle = {
                width: '4px',
                // opacity: 0.5
            }
            return {
                nftList, thumbStyle, toggleGroupDialog, groupDialog
            }
        }

    }
</script>
<style lang="">

</style>