import { createStore } from 'vuex'
import user from './modules/user'
import conversation from './modules/conversation'
import group from './modules/group'

export default createStore({
  state: {
    current: Date.now(), // 当前时间
    // intervalID: 0,
    // message: undefined,
    balance: '',
    account: '',
    nftList: [],
    currentNFTName: 'Conflux矿工纪念',
    currentNFTAddress: '',
    currentNFTGroups: [],  
  },
  getters: {
    hidden(state) {
      // eslint-disable-next-line no-unused-vars
      const temp = state.current
      if (typeof document.hasFocus !== 'function') {
        return document.hidden
      }

      // 文档是否在焦点
      // console.log('hasFocus:' + document.hasFocus())
      // console.log('hidden:' + !document.hasFocus())
      return !document.hasFocus()

    }
  },
  mutations: {
    checkoutCurrentNFT(state, payload) {
      state.currentNFTName = payload.name;
      state.currentNFTAddress = payload.address;
    },
    checkoutCurrentNFTGroups(state, groups) {
      state.currentNFTGroups = groups;
    }

  },
  actions: {
  },
  modules: {
    user,
    conversation,
    group
  }
})
