<template lang="">
    <div class='col-3'>
        <div class="q-pa-md row items-start q-gutter-md" dark>
            <q-card class="my-card" flat bordered>
                <q-img :src="group.image" />

                <q-card-section class="q-pb-none">
                    <div class="text-h5 q-mt-sm q-mb-xs">{{group.name}}</div>
                    <div class="text-caption text-grey">
                        Lorem ipsum dolor sit amet, consectetur adipiscing elit,
                    </div>

                </q-card-section>

                <q-card-actions align="right">
                    <q-btn flat color="grey" label="Buy NFT" />
                    <q-space />

                    <q-btn flat color="white" label="加入群组" @click="joinGroup" />

                </q-card-actions>

            </q-card>
        </div>
    </div>
</template>
<script>
    import { getCurrentInstance, computed } from 'vue'
    import { useQuasar } from 'quasar'
    import { useStore } from 'vuex'

    import conflux from '../../utils/conflux.js'
    import abi from '../../utils/abi/CFXCAUC1.json'
    export default {
        name: 'NFTGroupItem',
        props: {
            group: {
                type: Object
            }
        },
        setup(props) {
            const $q = useQuasar()
            const { appContext: { config: { globalProperties: { tim, TIM } } } } = getCurrentInstance()

            const store = useStore()

            const currentNFTAddress = computed(() => store.state.currentNFTAddress)

            const isLogin = computed(() => store.state.user.isLogin)

            const account = computed(() => store.state.account)

            async function joinGroup() {
                if (!isLogin.value) {
                    $q.notify({
                        message: '请登录',
                        icon: 'announcement',
                    })
                    return
                }
                let contract = conflux.Contract({
                    abi: abi,
                    address: currentNFTAddress.value
                })
                let ownArray = await contract.tokensOf(account.value)

                if (ownArray.length > 0) {
                    tim.joinGroup({
                        groupID: props.group.groupID,
                        type: TIM.TYPES.GRP_PUBLIC
                    }).then(function (imResponse) {
                        switch (imResponse.data.status) {
                            case TIM.TYPES.JOIN_STATUS_SUCCESS:
                                // 加入的群组资料
                                console.log(imResponse.data.gruop)
                                // 通知加群成功
                                $q.notify({
                                    message: '加入成功！',
                                    icon: 'announcement',
                                })
                                break;
                            case TIM.TYPES.JOIN_STATUS_ALREADY_IN_GROUP:
                                console.log('Already In Group')
                                // 通知已经在群内
                                $q.notify({
                                    message: '已经加入！',
                                    icon: 'announcement',
                                })
                                break;
                        }
                    }).catch(function (imError) {
                        console.warn('joinGroup Error', imError)
                    })
                    return

                }
                $q.notify({
                    message: '您还没有这款NFT！',
                    icon: 'announcement',
                })

            }
            return {
                joinGroup
            }
        }
    }
</script>
