<template lang="">
    



        <q-btn :label="connectWalletLabel" class="float-right" color="grey-9" @click="dialog = true" :disabled="isDisabled"/>

        <q-dialog v-model="dialog" persistent transition-show="flip-down" transition-hide="flip-up">
            <q-card class="bg-dark text-white" style="width: 400px">
                <q-card-section>
                    <div class="text-h6 text-center">链接登录Kind</div>
                </q-card-section>

                <q-card-section class="q-pt-none text-center">
                    <q-btn color="grey-9" @click="connectConfluxPortal">Conflux Portal</q-btn>
                </q-card-section>

                <q-card-actions align="right" class="text-white">
                    <q-btn flat label="Close" v-close-popup  />
                </q-card-actions>
              
            </q-card>
        </q-dialog>
</template>
<script>
    import confluxPortal from '../utils/conflux-portal.js'
    // import conflux from '../utils/conflux.js'
    // import { Drip } from 'js-conflux-sdk'
    import { ref, getCurrentInstance } from 'vue';
    // import store from '../store/index.js'
    import {useStore} from 'vuex'
    import {useRouter} from 'vue-router'
    export default {
        setup() {
            const isDisabled = ref(false);
            const connectState = ref('Conflux Portal');
            // const showRefreshBtn = ref(false);
            const connectWalletLabel = ref('连接钱包')
            const dialog = ref(false);
            const isConnecting = ref(false);
            const store = useStore()
            const router = useRouter()
            const { appContext: { config: { globalProperties: { tim, } } } } = getCurrentInstance()



            async function connectConfluxPortal() {
                connectState.value = '初始化中...'
                isConnecting.value = true
                await confluxPortal.enable()
                store.state.account = confluxPortal.getAccount()
                // store.state.account = conflux.wallet.addPrivateKey('0xC65937A18017FEDB2E690E919C7D35A164AFD61C8619EF439C8132D96821C5C6').address
                // console.log(store.state.account)
                // connectState.value = '已连接'
                // refreshBalance()
                dialog.value = false
                isDisabled.value = true
                // showRefreshBtn.value = true
                // dialog.value = false
                connectWalletLabel.value = store.state.account.slice(0, 6).toLowerCase() + '...' + store.state.account.slice(-3, -1).toLowerCase()
                login(store.state.account)
            }



            // async function refreshBalance() {
            //     if (!store.state.account) {
            //         console.log('not account ')
            //         return
            //     }
            //     store.state.balance = 'Loading...'
            //     console.log('refreshing...')
            //     store.state.balance = await conflux.getBalance(store.state.account)
            //     store.state.balance = Drip(store.state.balance).toCFX()
            // }

            function login(userID) {
                console.log(typeof (userID))
                tim.login({
                    userID: userID,
                    userSig: window.genTestUserSig(userID).userSig
                }).then(() => {
                    store.commit('toggleIsLogin', true)
                    console.log('I\'m Login')
                    // store.commit('startComputeCurrent')
                    // store.commit('showMessage', { type: 'success', message: '登录成功' })
                    // store.commit({
                    //     type: 'GET_USER_INFO',
                    //     userID: userId.value,
                    //     userSig: window.genTestUserSig(this.form.userID).userSig,
                    //     sdkAppID: window.genTestUserSig('').SDKAppID
                    // })
                    // store.commit('showMessage', {
                    //     type: 'success',
                    //     message: '登录成功'
                    // })
                    router.push('/kindChat')
                })
            }


            return {
                // connectConfluxPortal, showRefreshBtn, 
                // connectState, isDisabled, dialog,
                // account, isConnecting,
                connectWalletLabel,
                connectConfluxPortal,isDisabled,
                dialog
            };
       
        }
    }
</script>
<style scoped>
    .v-progress-circular {
        margin: 1rem;
    }
</style>