<template lang="">
    <q-item clickable v-ripple @click="handleGroupClick" class="q-ma-xs rounded-borders">
        <q-item-section avatar>
            <q-avatar>
                <img :src="groupAvatar">
            </q-avatar>
        </q-item-section>

        <q-item-section>
            <q-item-label caption lines="1" style="font-weight: bold; font-size: 0.8rem;">
                {{formatName(group.name)}}
            </q-item-label>
            <!-- <q-item-label class="conversation__summary" caption>
                <q-icon name="check" />
                'I\'m working on Quasar!'
            </q-item-label> -->
        </q-item-section>

        <q-item-section side>
            <q-icon name="keyboard_arrow_down" />
        </q-item-section>
    </q-item>

</template>
<script>
    import { getCurrentInstance, computed } from 'vue'
    import { useStore } from 'vuex'

    export default {
        name: 'GroupItem',
        props: ['group'],
        setup(props) {
            const { appContext: { config: { globalProperties: { tim } } } } = getCurrentInstance()
            console.log(props.group)
            const store = useStore();
            const groupAvatar = computed(() => {
                if (!props.group.avatar) {
                    return 'http://tspace-1253470014.cos.ap-hongkong.myqcloud.com/tspace/image/1399299885697732608.jpg'
                }
                return props.group.avatar
            })
            function quitGroup() {
                tim.quitGroup(props.group.groupID)
                    .catch(error => {
                        store.commit('showMessage', {
                            type: error,
                            message: error.message
                        })
                    })
            }
            
            function handleGroupClick() {
                const conversationID = `GROUP${props.group.groupID}`
                store.dispatch('checkoutConversation', conversationID)
            }

            function formatName(name) {
                if(name.length > 16) {
                    return name.slice(0,16) + '...'
                }
                return name
            } 
            return {
                quitGroup,groupAvatar, handleGroupClick, formatName
            }
        }
    }
</script>
