<template>

    <div v-if="showCurrentConversation">
        <q-header elevated class="bg-grey-10">
            <q-toolbar elevated>
                <q-btn dense flat round icon="forum" @click="toggleLeftDrawer">
                    <q-badge color="red" floating transparent v-if="totalUnreadCount != 0">
                        {{totalUnreadCount}}
                    </q-badge>
                </q-btn>
                <q-toolbar-title>
                    <avatar :src="avatar" :type="currentConversation.type" />
                    <span class="text-subtitle2 text-weight-bold q-pl-md">{{name}}</span>
                </q-toolbar-title>

                <q-btn round flat icon="more_vert">
                    <q-menu auto-close transition-show="jump-down" transition-hide="jump-up" :offset="[70, 0]">
                        <q-list style="min-width: 120px">
                            <q-item clickable>
                                <q-item-section>Contact Message</q-item-section>
                            </q-item>
                            <q-item clickable>
                                <q-item-section>Block</q-item-section>
                            </q-item>
                            <q-item clickable>
                                <q-item-section>Select Message</q-item-section>
                            </q-item>
                            <q-item clickable>
                                <q-item-section>Clear Message</q-item-section>
                            </q-item>
                        </q-list>
                    </q-menu>
                    <q-tooltip :offset="[0,5]">
                        Settings
                    </q-tooltip>
                </q-btn>
                <q-btn dense flat round icon="people_alt" @click="toggleRightDrawer" >
                    <q-tooltip :offset="[0,5]">
                        Member List
                    </q-tooltip>
                </q-btn>

                <q-input standout="bg-grey-9" v-model="text" dense class="q-px-sm" dark input-class="text-white"
                    placeholder="Search">
                    <template v-slot:append>
                        <q-icon name="search" class="text-grey" />
                    </template>
                </q-input>
                <q-btn dense flat round icon="inbox">
                    <q-tooltip :offset="[0,5]">
                        收件箱
                    </q-tooltip>
                </q-btn>
                <q-btn dense flat round icon="help">
                    <q-tooltip :offset="[0,5]">
                        帮助
                    </q-tooltip>
                </q-btn>
            </q-toolbar>
        </q-header>
        <q-scroll-area class="bg-grey-10 text-white rounded-borders q-px-md row justify-center" :visible="false"
            delay="1200" :thumb-style="thumbStyle" :style="{height: scrollAreaHeight + 'vh', 'max-width': '100%' }"
            ref="scrollAreaRef" @scroll="onScroll">
            <div class="justify-center row q-pt-sm">
                <q-btn label="查看更多" size="sm" flat v-if="!isCompleted" class="text-grey"
                    @click="store.dispatch('getMessageList', currentConversation.conversationID)" />
                <span v-else class="text-caption text-grey">没有更多了</span>
            </div>

            <message-item v-for="message in currentMessageList" :key="message.ID" :message="message"
                @loaded="onLoaded" />
        </q-scroll-area>


        <q-page-sticky position="bottom" :offset="[0, 15]">
            <q-btn flat size="sm" class="text-grey" v-show="isShowScrollBottomTips" @click="scrollMessageListToBottom">
                回到最新位置
            </q-btn>
        </q-page-sticky>

        <q-footer>
            <message-send-box @scroll-bottom="scrollMessageListToBottom"> </message-send-box>
        </q-footer>
        <!-- </div> -->
    </div>
    <div v-else>
        <q-toolbar class="bg-grey-10" elevated>
            <q-btn dense flat round icon="forum" @click="toggleLeftDrawer" />

        </q-toolbar>
    </div>
</template>
<script>
    import { computed, ref, nextTick, watch } from 'vue';
    import { useStore } from 'vuex'
    import MessageItem from '../message/MessageItem'
    import Avatar from '../Avatar.vue'
    import MessageSendBox from '../../components/message/MessageSendBox'
    export default {
        name: 'CurrentConversation',
        emits: ['toggleRightDrawer', 'toggleLeftDrawer'],
        components: {
            MessageItem,
            Avatar,
            MessageSendBox
        },
        setup(props, context) {

            // const { appContext: { config: { globalProperties: { tim } } } } = getCurrentInstance()

            const store = useStore()

            const currentMessageList = computed(() => store.state.conversation.currentMessageList);
            const currentConversation = computed(() => store.state.conversation.currentConversation)
            // const currentUnreadCount = computed(() => store.state.conversation.currentUnreadCount)
            const isCompleted = computed(() => store.state.conversation.isCompleted)
            // const isShowConversationList = computed(()=> store.state.conversation.isShowConversationList)

            const showCurrentConversation = computed(() => !!currentConversation.value.conversationID)

            const totalUnreadCount = computed(() => store.getters.totalUnreadCount)

            const isShowScrollBottomTips = ref(true)



            const toAccount = computed(() => store.getters.toAccount)
            // console.log(document.body.clientHeight)
            // const clientHeight = ref()
            // 聊天框高度
            const scrollAreaHeight = computed(() => {
                // console.log(document.body.clientHeight)
                let clientHeight = document.body.clientHeight;
                return (clientHeight - 100) / clientHeight * 100
            })
            const avatar = computed(() => {
                switch (currentConversation.value.type) {
                    case 'GROUP':
                        return currentConversation.value.groupProfile.avatar
                    case 'C2C':
                        // console.log(currentConversation.value.userProfile.avatar)
                        return currentConversation.value.userProfile.avatar
                    default:
                        return ''
                }
            })

            const name = computed(() => {
                if (currentConversation.value.type === 'C2C') {
                    let name = currentConversation.value.userProfile.nick || toAccount.value
                    let list = currentMessageList.value
                    let len = list.length
                    for (let i = len - 1; i >= 0; i--) {
                        // C2C 会话对端更新nick时需要更新会话title
                        if (list[i].flow === 'in' && list[i].nick && name !== list[i].nick) {
                            name = list[i].nick
                            break
                        }
                    }
                    return name
                } else if (currentConversation.value.type === 'GROUP') {
                    return currentConversation.value.groupProfile.name || toAccount.value
                } else if (currentConversation.value.conversationID === '@TIM#SYSTEM') {
                    return '系统通知'
                }
                return toAccount
            })

            watch(() => name.value, () => {
                setTimeout(() => {
                    scrollMessageListToBottom()
                }, 100)

            })

            function toggleRightDrawer() {
                context.emit('toggleRightDrawer')
            }

            function toggleLeftDrawer() {
                context.emit('toggleLeftDrawer')
            }


            // const preScrollHeight = ref(0)
            // const positionX = ref(0)
            // const positionY = ref(0)

            // function move(e) {

            // }

            const scrollAreaRef = ref(null)

            function onScroll(event) {
                // console.log(event)
                // console.log(event.ver)
                // console.log('scrolling');
                // scrollAre
                // console.log(scroll)
                if (!scrollAreaRef.value) {
                    return
                }
                // 0 
                if (event.verticalPercentage > 0.98) {
                    isShowScrollBottomTips.value = false
                }
            }

            // 如果滚到底部就保持在底部，否则提示要滚到底部
            function keepMessageListOnBottom() {
                // console.log(scrollAreaRef.value)
                if (!scrollAreaRef.value) {
                    return
                }
                // console.log(scrollAreaRef.value)
                // 距离底部20px内强制滚到底部，否则提示有新消息
                if (scrollAreaRef.value.getScrollPercentage().top > 0.8) {
                    nextTick(() => {
                        scrollAreaRef.value.setScrollPercentage('vertical', 1.3)
                    })
                    isShowScrollBottomTips.value = false
                } else {
                    isShowScrollBottomTips.value = true
                }
            }

            function onLoaded() {
                keepMessageListOnBottom()
            }


            function scrollMessageListToBottom() {
                nextTick(() => {

                    if (!scrollAreaRef.value) {
                        return
                    }
                    // let scrollHeight = scrollAreaRef.value.get
                    scrollAreaRef.value.setScrollPercentage('vertical', 1.8)
                    // preScrollHeight.value = scrollAreaNode.scrollHeight
                    // isShowScrollBottomTips.value = false
                })
            }

            // setInterval(() => {
            //     console.log('Storehidden: ' + store.getters.hidden)
            //     console.log('hidden:' + hidden.value)
            // }, 1000)
            // const currentUnreadCount = computed(() => store.state.conversation.currentConversation.unreadCount)
            // const hidden = computed(() => store.getters.hidden)

            // // setInterval(() => {
            // //     console.log('StoretotalUreadCount: ' + store.getters.totalUnreadCount)
            // //     console.log('totalUnreadCount:' + totalUnreadCount.value)
            // // }, 1000)
            // watch(currentUnreadCount, (next) => {
            //     if (!hidden.value && next > 0) {
            //         tim.setMessageRead({ conversationID: currentConversation.value.conversationID })
            //     }
            //     console.log('Count change')
            // })
            // watch(() => store.getters.hidden, () => {

            //     console.log('hidden is change')
            // }, {
            //     deep: true
            // })

            // watch(() => store.getters.totalUnreadCount, () => {
            //     console.log('change')
            // }, {
            //     deep: true
            // })

            watch(() => store.getters.hidden, () => {
                // console.log('cool')
            }, {
                deep: true
            })

            const thumbStyle = {
                width: '4px',
                // opacity: 0.5
            }



            return {
                currentMessageList, name, store,
                currentConversation, isCompleted, avatar,
                toggleLeftDrawer, toggleRightDrawer, isShowScrollBottomTips, onScroll,
                scrollMessageListToBottom, showCurrentConversation, scrollAreaHeight,
                totalUnreadCount, scrollAreaRef,
                thumbStyle,
                onLoaded
            }

        }
    }
</script>
<style>
    .newMessageTips {
        position: absolute !important;
        padding: 5px;
        width: 120px;
        margin: auto;
        left: 0;
        right: 0;
        bottom: 5px;
        font-size: 12px;
        text-align: center;
        border-radius: 10px;
    }
</style>