<template>
    <q-chat-message :avatar="avatar" :stamp="date"  text-color="white" :bg-color="bgColor" :sent="isMine" :name="name">
        <message-content :contentList="contentList" />
    </q-chat-message>
</template>
<script>
    import { computed } from 'vue'
    import { decodeText } from "../../../utils/decodeText.js"
    import MessageContent from "./MessageContent.vue"
    // import { useStore } from 'vuex'

    export default {
        name: "TextElement",
        components: {
            MessageContent
        },
        props: {
            payload: {
