                required: true
            },
            // 用于消息框下拉撤回部分
            // message: {
            //     type: Object,
            //     required: true,
            // },
            isMine: {
                type: Boolean
            },
            avatar: {
                type: String,
            },
            name: {
                type: String
            },
            date: {
                
            }


        },
        setup(props) {
            // const store = useStore()
            // console.log(props.name)
            const contentList = computed(() => {
                return decodeText(props.payload);
            })
            const bgColor = computed(()=> {
                return props.isMine ?  'grey-8': 'grey-8'
            })

            // Need to do here
            // const contentListLength = computed(() => {
            //     let totalContent = '';
            //     for(let content of contentList.value) {
            //         totalContent += content.text
            //     }
            //     return totalContent.length
            // })

            // const autoSize = computed(()=> {
            //     if(contentListLength > 5) {
            //         return 5
            //     } else {
            //         return 3
            //     } 
            // })

            return {
                contentList, bgColor
            }
            
        }
    }
</script>
