<template lang="">
    <div>
        <q-toolbar class="row bg-grey-10 ">
            <q-input standout="bg-grey-9" input-class="text-white" dense class="col-grow" placeholder="Type a message" v-model="messageContent"
                @keydown.enter.exact.prevent="handleEnter">
                <!-- <template v-slot:before>
                    <q-avatar>
                        <img src="https://cdn.quasar.dev/img/avatar5.jpg">
                    </q-avatar>
                </template> -->

                <template v-slot:append >
                    <q-icon v-if="messageContent !== ''" name="close" @click="messageContent = ''" class="cursor-pointer text-grey" />
                    <q-btn round dense flat icon="insert_emoticon" class="text-grey"/>
                    <q-btn round dense flat icon="send" class="text-grey" @click="sendTextMessage">
                        <q-tooltip>
                            按Enter发送消息
                        </q-tooltip>
                    </q-btn>
                </template>



                
            </q-input>
            

        </q-toolbar>
    </div>
</template>
<script>
    import { ref, computed, getCurrentInstance } from "vue"
    // import { useRouter } from "vue-router"
    import { useStore, mapGetters } from "vuex"

    export default {
        computed: {
            ...mapGetters(['currentConversationType']),
        },
        setup(props, context) {

            const store = useStore();
            // const route = useRouter();
            const currentConversationType = computed(() => store.getters.currentConversationType);
            const toAccount = computed(() => store.getters.toAccount);

            let messageContent = ref('');
            let callingList = ref([]);
            const groupAt = ref(false);
            const { appContext: { config: { globalProperties: { tim, TIM } } } } = getCurrentInstance()


            function sendTextMessage() {

                if (
                    messageContent.value === '' ||
                    messageContent.value.trim().length === 0
                ) {
                    messageContent.value = ''
                    store.commit('showMessage', {
                        message: '不能发送空消息哦！',
                        type: 'info'
                    })
                    return
                }
                // 发送群组消息
                if (currentConversationType.value === TIM.TYPES.CONV_GROUP && groupAt.value) {

                    let message = this.tim.createTextAtMessage({
                        to: toAccount,
                        // 会话类型群组
                        conversationType: TIM.TYPES.CONV_GROUP,
                        payload: {
                            text: messageContent.value,
                            atUserList: callingList.value // 'denny' 'lucy' 都是 userID，而非昵称
                        }
                    })
                    store.commit('pushCurrentMessageList', message)
                    // 发送消息后滑倒底部  后续实现
                    // this.$bus.$emit('scroll-bottom')
                    tim.sendMessage(message).catch(error => {
                        store.commit('showMessage', {
                            type: 'error',
                            message: error.message
                        })
                    })

                    messageContent.value = ''
                    groupAt.value = false
                    context.emit('scroll-bottom')

                    return
                }
                const message = tim.createTextMessage({
                    to: toAccount.value,
                    conversationType: currentConversationType.value,
                    payload: { text: messageContent.value }
                })
                store.commit('pushCurrentMessageList', message)
                // this.$bus.$emit('scroll-bottom')
                tim.sendMessage(message).then(() => {
                    console.log('send Successfully')
                }).catch(error => {
                    store.commit('showMessage', {
                        type: 'error',
                        message: error.message
                    })
                })
                messageContent.value = ''
                context.emit('scroll-bottom')

            }

            function handleEnter() {
                sendTextMessage()
            }
            return {
                sendTextMessage, messageContent, handleEnter
            }

        }
    }
</script>
<style lang="">

</style>