<template lang="">
    <q-header elevated class="bg-grey-10">
        <q-toolbar>

            <div class="col-12">
                <q-btn round flat icon="menu" @click="toggleLeftDrawer" />
                <conflux-portal></conflux-portal>
            </div>
        </q-toolbar>
    </q-header>
   
    <!-- <q-btn label="testContract" @click="test"></q-btn> -->
    <nft-group-content :groups="groups"></nft-group-content>

</template>
<script>
    import { computed, } from 'vue'
    import nftGroupContent from '../nft/NFTGroupContent'
    import confluxPortal from '../ConfluxPortal'
    import { useStore } from 'vuex'

    import conflux from '../../utils/conflux.js'
    import abi from  '../../utils/abi/CFXCAUC1.json'


    export default {
        name: 'NFTGroupContainer',
        components: {
            nftGroupContent,
            confluxPortal,
        },
        emits: ['toggleLeftDrawer'],
        setup(props, context) {
            const store = useStore()
            // const { appContext: { config: { globalProperties: { tim, TIM } } } } = getCurrentInstance()

            function toggleLeftDrawer() {
                context.emit('toggleLeftDrawer');
            }
           
            const groups = computed(()=> store.state.currentNFTGroups)


            async function test() {
                
                let contract = conflux.Contract({
                    abi: abi,
                    address: store.state.nftList[0].address
                })
                
                console.log(contract)
                console.log(store.state.account)
                // 返回一个拥有的nft的数组
                let data = await contract.tokensOf(store.state.account)
                let total = await contract.totalSupply()
                console.log(total[0])
                console.log(data.length > 0)
                // let result = data.toString()
                // console.log(result)
            }
        
            

            return {
                groups, toggleLeftDrawer, test,

            }
        }
    }
</script>
