<template lang="">
    <q-item clickable v-ripple @click="checkoutCurrentNFT(nft.name, nft.address)" class="q-ma-xs rounded-borders"
        :active="isActive" active-class="bg-grey-9 text-white">
        <q-item-section avatar v-if="isImage">
            <q-avatar>
                <img :src="nft.image">
            </q-avatar>
        </q-item-section>
        <q-item-section avatar v-else>
            <q-avatar>
                {{nft.name.slice(0,1)}}
            </q-avatar>

        </q-item-section>

        <q-item-section>
            <q-item-label caption lines="1" style="font-size:0.9rem; font-weight: bold;">
                {{formatName(nft.name)}}
            </q-item-label>
            <!-- <q-item-label class="conversation__summary" caption>
                <q-icon name="check" />
                'I\'m working on Quasar!'
            </q-item-label> -->
        </q-item-section>


        <q-item-section side>
            <q-btn class="q-mr-sm circle" size="sm" dense flat color="grey-9" icon="add" @click="showCreateGroupDialog">
                <q-tooltip anchor="center right" self="center left">
                    创建群组
                  </q-tooltip>
            </q-btn>
        </q-item-section>
    </q-item>


</template>
<script>
    import { useStore } from 'vuex'
    import { computed } from 'vue'
    export default {
        name: 'NFTItem',
        props: {
            nft: {
                type: Object
            }
        },
        emits: ['toggleGroupDialog'],
        setup(props, context) {
            const store = useStore()
            function checkoutCurrentNFT(name, address) {
                console.log(address)
                store.commit('checkoutCurrentNFT', {
                    name,
                    address
                })
                let url = 'http://192.168.88.129:8000/api/nft/groups/?address=' + address

                fetch(url).then((resp) => {
                    return resp.json()
                }).then((groups) => {
                    console.log(groups);
                    store.commit('checkoutCurrentNFTGroups', groups)
                })
            }

            const isImage = computed(() => {
                let format = props.nft.image.split('.').pop()
                if (format == 'jpg' || format == 'png' || format == 'gif' || format == "jpeg") {
                    return true
                }
                return false
            })

            const isActive = computed(() => {
                if (store.state.currentNFTName == props.nft.name) {
                    return true
                }
                return false
            })

            function formatName(name) {
                if (name.length > 14) {
                    return name.slice(0, 14) + '...'
                }
                return name
            }

            function showCreateGroupDialog() {
                context.emit('toggleGroupDialog')
            }

            return {
                store, checkoutCurrentNFT, isImage, formatName, isActive, showCreateGroupDialog
            }
        }
    }
</script>
