<template lang="">
    <q-avatar :size="size">
        <img :src="avatarSrc" >
    </q-avatar>
</template>
<script>
    import {computed, getCurrentInstance} from 'vue'
    import systemAvatar from '../assets/image/system.png'
    export default {
        props: {
            src: String,
            type: {
                type: String,
                default: 'C2C'
            },
            shape: {
                type: String,
                default: 'circle'
            },
            size: {
                type: String
            }
        },
        setup(props) {
            const { appContext: { config: { globalProperties: { TIM } } } } = getCurrentInstance()
            const avatarSrc = computed(() => {
                if (/^(https:|http:|\/\/)/.test(props.src)) {
                    return props.src
                } else {
                    return defaultSrc.value
                }
            })

            const defaultSrc = computed(() => {
                switch(props.type) {
                    // 个人头像
                    case 'C2C':
                        return  'https://tspace-1253470014.cos.ap-hongkong.myqcloud.com/nft/confienergy/image/coolzai.gif'
                    // 群默认头像 
                    case 'GROUP':
                        return 'http://tspace-1253470014.cos.ap-hongkong.myqcloud.com/tspace/image/1399299885697732608.jpg'
                    case TIM.TYPES.CONV_SYSTEM:
                        return systemAvatar
                    default: 
                        // 默认头像
                        return 'https://gateway.pinata.cloud/ipfs/QmT9dYDsNVL6FoFQuWS66YHpCbWr9KdV63V5Uxxo3YeGQ2/ctn-5.png'
                }
            })
            return {
                avatarSrc
            }
        }
    }
</script>
